/* 歌曲播放中状态 */

.song-playing:before {
    position: absolute;
    left: 0;
    top: 8px;
    width: 14px;
    height: 14px;
    content: '';
    background: url(data:image/png;base64,) no-repeat center;
}

/* 没有歌曲播放的时候，显示的状态 */

.song-no-played {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 99%;
    height: 94%;
    background: #fff url(../../static/img/no-data-icon.4958bbd.png) no-repeat center center;
    background-size: 120px auto;
}

.song-no-played span {
    position: absolute;
    top: 60%;
    left: 0;
    width: 100%;
    text-align: center;
}

/* 歌曲播放、暂停 */

.song-play-pause {
    background: url(data:image/png;base64,) no-repeat center center;
    background-size: 22px;
}

/* 歌曲播放中 */

.song-played {
    background: url(../../static/img/music.playing.66c95de.png) no-repeat center center;
    background-size: 20px;
}
